import React, {useState} from 'react'
import { PrinterOutlined, CloseOutlined, CheckOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Button, Tag, Alert } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import Flex from 'components/shared-components/Flex'
 
const { Column } = Table; 
const Invoice = (props) => { 	
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/garage/list`)
	}	
	
	const { toolsUsed, garage, allCurrency, allTrucks, allCurrencyLoading } = props;

	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img src="/img/logo.png" alt=""/>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">Great lakes</span><br />
								<span>Truck: {allTrucks[garage.data().truckId].model + ' ' + allTrucks[garage.data().truckId].plateNumber}</span><br />
								<span>Tools : {garage.data().tools}</span><br />
								<span>Amount : {garage.data().amount} {allCurrencyLoading ? '' : allCurrency[garage.data().currency]?.name}</span><br />
								<span>Exchange Rate : <NumberFormat displayType={'text'} value={garage.data().exchangeRate} thousandSeparator={true} /></span><br />
								<span>Amount in RWF : <NumberFormat displayType={'text'} value={garage.data().totalAmount} suffix=" Rwf" thousandSeparator={true} /></span><br />
								<span>Description : {garage.data().description}</span><br />
							</p>
						</address>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Garage activities Details</h2>
						<p>{new Date(garage.data().date).toLocaleDateString()}</p>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{garage.garageName}</span><br />
							</p>
						</address>
					</div>
				</div>
				

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Tools Used</h2>
					<Table dataSource={toolsUsed} pagination={false} className="mb-5">
						
						<Column title="Name" 
							render={(_,record) => record.data().toolName} 
							key="toolName"
						/>
						
						
						<Column title="Quantity" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.data().quantity} 
									thousandSeparator={true}
								/>
							)} 
							key="quantity"
						/>
						<Column 
							title="Unit Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().price} 
									suffix={allCurrencyLoading ? '' : allCurrency[garage.data().currency]?.name}
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Total Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().total} 
									suffix={allCurrencyLoading ? '' : allCurrency[garage.data().currency]?.name}
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						
					</Table>
				</div>
				
				<div className="mt-3">
					
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={garage.data().totalAmount} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div>

				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>

			</Card>
		</div>
	);
}

export default Invoice
