import React, { useState } from 'react'
import { Alert, Card, Table, Button, Tooltip, Modal, Row, Col, Select, Form, DatePicker, InputNumber, message, Input, Menu, Dropdown } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import { PlusCircleOutlined, DownloadOutlined, CheckCircleOutlined } from '@ant-design/icons';
import { DataService as ReqRentService } from "services/reqRent.service";
import { DataService as ReqMilleageService } from "services/reqMilleage.service";
import { DataService as ReqFuelService } from "services/reqFuel.service";
import { DataService as ReqClearingService } from "services/reqClearing.service";
import { DataService as ReqSalaryService } from "services/reqSalary.service";
import { DataService as ReqRoadtallService } from "services/reqRoadtall.service";

import moment from "moment";
import GarageList from '../invoice';

const { Title } = Typography;
const dateFormat = 'DD/MM/YYYY';
const { confirm } = Modal;
const { Option } = Select;
const { MonthPicker } = DatePicker;

const Garage = ({systemData}) => {
	let history = useHistory();
	
  	const { allTrucks, garageTools, garageToolsLoading, allCurrencyLoading, allCurrency, reqToRentLoading, reqToRent, reqTypes, reqTypesLoading, reqRent, reqRentLoading, trucks, trucksLoading, destinations, destinationsLoading, reqMilleage, reqMilleageLoading, allDestinationsLoading, allDestinations, reqFuel, reqFuelLoading, reqClearing, reqClearingLoading, reqSalary, reqSalaryLoading, employees, employeesLoading, allEmployees, allEmployeesLoading, allBorders, reqRoadtall, reqRoadtallLoading, borders, bordersLoading,} = systemData;
	const [toolsUsed, setToolsUsed] = useState(null);
	const [listVisible, setListVisible] = useState(false);
	const [show, setShow] = useState(false);
	const [showRent, setShowRent] = useState(false);
	const [showMilleage, setShowMilleage] = useState(false);
	const [showFuel, setShowFuel] = useState(false);
	const [showClearing, setShowClearing] = useState(false);
	const [showSalary, setShowSalary] = useState(false);
	const [showRoadtall, setShowRoadtall] = useState(false);
	const [updateKey, setUpdateKey] = useState();
	const [updateMilleageKey, setUpdateMilleageKey] = useState();
	const [updateFuelKey, setUpdateFuelKey] = useState();
	const [updateClearingKey, setUpdateClearingKey] = useState();
	const [updateSalaryKey, setUpdateSalaryKey] = useState();
	const [updateRoadtallKey, setUpdateRoadtallKey] = useState();
	const [confirmLoading, setConfirmLoading] = useState(false);
	const [month, setMonth] = useState();
	const [error, setError] = useState();
	const [garage, setGarage] = useState({});
	const [liter, setLiter] = useState(0);
	const [rate, setRate] = useState(1);
	const [price, setPrice] = useState(0);
	const [totalAmount, setTotalAmount] = useState(0);
	
	const [form] = Form.useForm();
	const todayDate = new Date();
	let tYear = todayDate.getFullYear();
	let tMonth = todayDate.getMonth()+1;
	let tDate = todayDate.getDate();
	tMonth = tMonth < 9 ? '0'+tMonth : tMonth;
	const [reqId, setReqId] = useState('reqId'+tYear+tMonth+tDate);

	let rentTotal = !reqRentLoading ? reqRent.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().rentAmount);
	},0) : 0;

	let salaryTotal = !reqSalaryLoading ? reqSalary.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().salaryAmount);
	},0) : 0;

	let milleageTotal = !reqMilleageLoading ? reqMilleage.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().milleageAmount);
	},0) : 0;

	let fuelTotal = !reqFuelLoading ? reqFuel.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().fuelAmount);
	},0) : 0;

	let clearingTotal = !reqClearingLoading ? reqClearing.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().clearingAmount);
	},0) : 0;

	let roadtallTotal = !reqRoadtallLoading ? reqRoadtall.docs.filter(x=> x.data().reqId == reqId).reduce((accumulator, currentElement) => {
		return parseFloat(accumulator) + parseFloat(currentElement.data().roadtallAmount);
	},0) : 0;

	let grandTotal = rentTotal+salaryTotal+milleageTotal+fuelTotal+clearingTotal+roadtallTotal;

	const deleteRow = row => {
		ReqRentService.remove(row._id);
		message.success({ content: `Requested rent for ${row.rentItem} deleted `, duration: 3 });
	}

	const deleteMilleage = row => {
		ReqMilleageService.remove(row._id);
		message.success({ content: `Requested milleage of ${row.milleageAmount} deleted `, duration: 3 });
	}

	const deleteFuel = row => {
		ReqFuelService.remove(row._id);
		message.success({ content: `Requested fuel of ${row.fuelAmount} deleted `, duration: 3 });
	}

	const deleteClearing = row => {
		ReqClearingService.remove(row._id);
		message.success({ content: `Requested clearing of ${row.clearingAmount} deleted `, duration: 3 });
	}

	const deleteSalary = row => {
		ReqSalaryService.remove(row._id);
		message.success({ content: `Requested salary of ${row.salaryAmount} deleted `, duration: 3 });
	}

	const deleteRoadtall = row => {
		ReqRoadtallService.remove(row._id);
		message.success({ content: `Requested roadtall of ${row.roadtallAmount} deleted `, duration: 3 });
	}


	function showConfirm(row) {
		confirm({
		  title: `Do you want to delete this rent for ${row.rentItem}?`,
		  onOk() {
			deleteRow(row)
		  },
		  onCancel() {},
		});
	}

	function showConfirmMilleage(row) {
		confirm({
		  title: `Do you want to delete this milleage for ${row.milleageAmount}?`,
		  onOk() {
			deleteMilleage(row)
		  },
		  onCancel() {},
		});
	}
	
	function showConfirmFuel(row) {
		confirm({
		  title: `Do you want to delete this fuel request?`,
		  onOk() {
			deleteFuel(row)
		  },
		  onCancel() {},
		});
	}
	
	function showConfirmClearing(row) {
		confirm({
		  title: `Do you want to delete this clearing of ${row.clearingAmount}?`,
		  onOk() {
			deleteClearing(row)
		  },
		  onCancel() {},
		});
	}
	
	function showConfirmSalary(row) {
		confirm({
		  title: `Do you want to delete this salary of ${row.salaryAmount}?`,
		  onOk() {
			deleteSalary(row)
		  },
		  onCancel() {},
		});
	}
			
	function showConfirmRoadtall(row) {
		confirm({
		  title: `Do you want to delete this roadtall of ${row.roadtallAmount}?`,
		  onOk() {
			deleteRoadtall(row)
		  },
		  onCancel() {},
		});
	}

	const newRequisition = () => {
		setShow(true)
	}

	const closeOthers = () => {
		setShowRent(false);
		setShowMilleage(false);
		setShowFuel(false);
		setShowClearing(false);
		setShowSalary(false);
		setShowRoadtall(false);
		setUpdateKey();
		setUpdateMilleageKey();
		setUpdateFuelKey();
		setUpdateClearingKey();
		setUpdateSalaryKey();
		setUpdateRoadtallKey();
		setError();
		setMonth();
	}

	const cancelRequisition = () => {
		setShow(false)
	}

	const updateRent = (row) => {

		let sDate = row.rentMonth;
		let fullDate=`${new Date(sDate).getFullYear()}-${((new Date(sDate).getMonth())+1) < 10 ? '0' : ''}${(new Date(sDate).getMonth())+1}}`
		setMonth(fullDate);
		setUpdateKey(row._id);
		form.setFieldsValue({
			rentItem: row.rentItem,
			rentAmount: row.rentAmount,
			rentMonth: moment(fullDate, 'YYYY-MM'),
		});
		setShowRent(true);
	}

	
	const updateMilleage = (row) => {

		setUpdateMilleageKey(row._id);
		form.setFieldsValue({
			milleageTruck: row.milleageTruck,
			milleageDestination: row.milleageDestination,
			milleageAmount: row.milleageAmount
		});
		setShowMilleage(true);
	}

	const updateFuel = (row) => {

		setPrice(row.fuelPrice);
		setLiter(row.fuelLiters);
		setTotalAmount(row.fuelLiters*row.fuelPrice);
		setUpdateFuelKey(row._id);
		form.setFieldsValue({
			fuelTruck: row.fuelTruck,
			fuelDestination: row.fuelDestination,
			fuelLiters: row.fuelLiters,
			fuelPrice: row.fuelPrice,
		});
		setShowFuel(true);
	}

	const updateClearing = (row) => {

		setUpdateClearingKey(row._id);
		form.setFieldsValue({
			clearingTruck: row.clearingTruck,
			clearingDestination: row.clearingDestination,
			clearingAmount: row.clearingAmount
		});
		setShowClearing(true);
	}

	const updateSalary = (row) => {

		let sDate = row.salaryMonth;
		let fullDate=`${new Date(sDate).getFullYear()}-${((new Date(sDate).getMonth())+1) < 10 ? '0' : ''}${(new Date(sDate).getMonth())+1}}`
		setMonth(fullDate);
		setUpdateKey(row._id);
		form.setFieldsValue({
			salaryEmployee: row.salaryEmployee,
			salaryAmount: row.salaryAmount,
			salaryMonth: moment(fullDate, 'YYYY-MM'),
		});
		setShowSalary(true);
	}

	const updateRoadtall = (row) => {

		setUpdateRoadtallKey(row._id);
		form.setFieldsValue({
			roadtallTruck: row.roadtallTruck,
			roadtallDestination: row.roadtallDestination,
			roadtallBorder: row.roadtallBorder,
			roadtallAmount: row.roadtallAmount
		});
		setShowRoadtall(true);
	}


	const onMonthChange = (date, dateString) => {
		setMonth(dateString);
  	}

	const handleReqRent = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let rentItem = values.rentItem;
			let rentMonth = month;
			let rentAmount = values.rentAmount;
			if(!rentItem) {
				setConfirmLoading(false);
				return message.error('Please select what rent for');
			}
			else if(!rentAmount || parseFloat(rentAmount) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter rent amount');
			}
			else if(!rentMonth) {
				setConfirmLoading(false);
				return message.error('Please select which month');
			}
			else if(reqRent.docs.filter(x=> x.data().rentItem == rentItem && x.data().rentMonth == rentMonth).length > 0){
				setConfirmLoading(false);
				setError('This item have already been requested, check and try again');
				return message.error('This item have already been requested, check and try again');
			}
			setTimeout(() => {
				let reqRentData = {
					rentItem,
					rentMonth, 
					rentAmount,
					reqId
				}
				if(!updateKey) ReqRentService.create(reqRentData);
				else ReqRentService.update(updateKey,reqRentData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Rent have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const handleReqMilleage = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let milleageTruck = values.milleageTruck;
			let milleageDestination = values.milleageDestination;
			let milleageAmount = values.milleageAmount;
			if(!milleageTruck) {
				setConfirmLoading(false);
				return message.error('Please select driver and truck');
			}
			else if(!milleageAmount || parseFloat(milleageAmount) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter milleage amount');
			}
			else if(!milleageDestination) {
				setConfirmLoading(false);
				return message.error('Please select which destination');
			}
			
			setTimeout(() => {
				let reqRentData = {
					milleageTruck: milleageTruck,
					milleageAmount, 
					milleageDestination: milleageDestination,
					reqId
				}
				if(!updateMilleageKey) ReqMilleageService.create(reqRentData);
				else ReqMilleageService.update(updateMilleageKey,reqRentData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Milleage have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const handleReqFuel = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let fuelTruck = values.fuelTruck;
			let fuelDestination = values.fuelDestination;
			let fuelLiters = values.fuelLiters;
			let fuelPrice = values.fuelPrice;
			if(!fuelTruck) {
				setConfirmLoading(false);
				return message.error('Please select driver and truck');
			}
			else if(!fuelLiters || parseFloat(fuelLiters) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter liters added');
			}
			else if(!fuelPrice || parseFloat(fuelPrice) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter price per liter');
			}
			else if(!fuelDestination) {
				setConfirmLoading(false);
				return message.error('Please select which destination');
			}
			
			setTimeout(() => {
				let reqFuelData = {
					fuelTruck,
					fuelLiters, 
					fuelDestination,
					fuelPrice,
					fuelAmount:totalAmount,
					reqId
				}
				if(!updateFuelKey) ReqFuelService.create(reqFuelData);
				else ReqFuelService.update(updateFuelKey,reqFuelData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Fuel have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const handleReqClearing = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let clearingTruck = values.clearingTruck;
			let clearingDestination = values.clearingDestination;
			let clearingAmount = values.clearingAmount;
			if(!clearingTruck) {
				setConfirmLoading(false);
				return message.error('Please select driver and truck');
			}
			else if(!clearingAmount || parseFloat(clearingAmount) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter clearing amount');
			}
			else if(!clearingDestination) {
				setConfirmLoading(false);
				return message.error('Please select which destination');
			}
			
			setTimeout(() => {
				let reqRentData = {
					clearingTruck,
					clearingAmount, 
					clearingDestination,
					reqId
				}
				if(!updateClearingKey) ReqClearingService.create(reqRentData);
				else ReqClearingService.update(updateClearingKey,reqRentData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Clearing have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const handleReqSalary = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let salaryEmployee = values.salaryEmployee;
			let salaryMonth = month;
			let salaryAmount = values.salaryAmount;
			if(!salaryEmployee) {
				setConfirmLoading(false);
				return message.error('Please select which employee to get salary');
			}
			else if(!salaryAmount || parseFloat(salaryAmount) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter salary amount');
			}
			else if(!salaryMonth) {
				setConfirmLoading(false);
				return message.error('Please select which month');
			}
			else if(reqSalary.docs.filter(x=> x.data().salaryEmployee == salaryEmployee && x.data().salaryMonth == salaryMonth).length > 0){
				setConfirmLoading(false);
				setError('This item have already been requested, check and try again');
				return message.error('This item have already been requested, check and try again');
			}
			setTimeout(() => {
				let reqSalaryData = {
					salaryEmployee,
					salaryMonth, 
					salaryAmount,
					reqId
				}
				if(!updateSalaryKey) ReqSalaryService.create(reqSalaryData);
				else ReqSalaryService.update(updateSalaryKey,reqSalaryData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Salary have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const handleReqRoadtall = () => {
		setConfirmLoading(true);
		setError();
		form.validateFields().then(values => {
			let roadtallTruck = values.roadtallTruck;
			let roadtallDestination = values.roadtallDestination;
			let roadtallAmount = values.roadtallAmount;
			let roadtallBorder = values.roadtallBorder;
			if(!roadtallTruck) {
				setConfirmLoading(false);
				return message.error('Please select driver and truck');
			}
			else if(!roadtallAmount || parseFloat(roadtallAmount) <= 0) {
				setConfirmLoading(false);
				return message.error('Please enter roadtall amount');
			}
			else if(!roadtallDestination) {
				setConfirmLoading(false);
				return message.error('Please select which destination');
			}
			else if(!roadtallBorder) {
				setConfirmLoading(false);
				return message.error('Please select the border');
			}

			setTimeout(() => {
				let reqRoadtallData = {
					roadtallTruck,
					roadtallAmount, 
					roadtallDestination,
					roadtallBorder,
					reqId
				}
				console.log(reqRoadtallData)
				if(!updateRoadtallKey) ReqRoadtallService.create(reqRoadtallData);
				else ReqRoadtallService.update(updateRoadtallKey,reqRoadtallData);
				setConfirmLoading(false);
				closeOthers();
				message.success(`Roadtall have been added on requisition`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}
	
	const handleChange = (value) => {
		closeOthers();
		switch (value) {
			case 'RENT':
				setShowRent(true);
				break;
			case 'MILLEAGE':
				setShowMilleage(true);
				break;
			case 'FUEL':
				setShowFuel(true);
				break;
			case 'CLEARING':
				setShowClearing(true);
				break;
			case 'SALARIES':
				setShowSalary(true);
				break;
			case 'ROADTALL':
				setShowRoadtall(true);
				break;					
			default:
				
				break;
		}
	}
	
	const closeGarageList = () => {
		setListVisible(false)
		setGarage({})
		setToolsUsed({});
	};

	const handleRateChange = (value) => {
		let total = value*liter*price;
		setRate(value);
		setTotalAmount(total);
	}
	
	const handleLiterChange = (value) => {
		let total = rate*price*value;
		setLiter(value);
		setTotalAmount(total);
	}
	
	const handlePriceChange = (value) => {
		let total = rate*liter*value;
		setPrice(value);
		setTotalAmount(total);
	}

	const rentColumns = [
		{
			title: 'Rent Item ',
			dataIndex: 'rentItem',
			render: (_,record) => record.data().rentItem
		},
		{
			title: 'Rent Month',
			dataIndex: 'rentMonth',
			render: (_, record) => record.data().rentMonth
		},
		{
			title: 'Amount',
			dataIndex: 'rentAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().rentAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateRent(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirm(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];

	const milleageColumns = [
		
		{
			title: 'Destination',
			dataIndex: 'milleageDestination',
			render: (_, record) => record.data().milleageDestination,
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().milleageDestination]?.name +' - '+allDestinations[record.data().milleageDestination]?.country}</Flex>
			),
		},
		
		{
			title: 'Truck and Driver',
			dataIndex: 'milleageTruck',
			render: (_,record) => `${allTrucks[record.data().milleageTruck].plateNumber} - ${allTrucks[record.data().milleageTruck].driverName}`
		},
		{
			title: 'Amount',
			dataIndex: 'milleageAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().milleageAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateMilleage(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirmMilleage(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];

	const fuelColumns = [
		{
			title: 'Truck and Driver',
			dataIndex: 'fuelTruck',
			render: (_,record) => `${allTrucks[record.data().fuelTruck].plateNumber} - ${allTrucks[record.data().fuelTruck].driverName}`
		},
		{
			title: 'Destination',
			dataIndex: 'fuelDestination',
			render: (_, record) => record.data().fuelDestination,
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().fuelDestination]?.name +' - '+allDestinations[record.data().fuelDestination]?.country}</Flex>
			),
		},
		{
			title: 'Liters',
			dataIndex: 'fuelLiters',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().fuelLiters} 
							  suffix={` L`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Price/L',
			dataIndex: 'fuelPrice',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().fuelPrice} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Amount',
			dataIndex: 'fuelAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().fuelAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateFuel(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirmFuel(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];

	const clearingColumns = [
		
		{
			title: 'Destination',
			dataIndex: 'clearingDestination',
			render: (_, record) => record.data().clearingDestination,
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().clearingDestination]?.name +' - '+allDestinations[record.data().clearingDestination]?.country}</Flex>
			),
		},
		{
			title: 'Truck and Driver',
			dataIndex: 'clearingTruck',
			render: (_,record) => `${allTrucks[record.data().clearingTruck].plateNumber} - ${allTrucks[record.data().clearingTruck].driverName}`
		},
		{
			title: 'Amount',
			dataIndex: 'clearingAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().clearingAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateClearing(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirmClearing(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];

	const salaryColumns = [
		{
			title: 'Employee',
			dataIndex: 'salaryEmployee',
			render: (_,record) => allEmployeesLoading ? '' : allEmployees[record.data().salaryEmployee]?.name
		},
		{
			title: 'Salary Month',
			dataIndex: 'salaryMonth',
			render: (_, record) => record.data().salaryMonth
		},
		{
			title: 'Amount',
			dataIndex: 'salaryAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().salaryAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateSalary(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirmSalary(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];

	const roadtallColumns = [
		{
			title: 'Truck and Driver',
			dataIndex: 'roadtallTruck',
			render: (_,record) => `${allTrucks[record.data().roadtallTruck].plateNumber} - ${allTrucks[record.data().roadtallTruck].driverName}`
		},
		{
			title: 'Destination',
			dataIndex: 'roadtallDestination',
			render: (_, record) => record.data().roadtallDestination,
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().roadtallDestination]?.name +' - '+allDestinations[record.data().roadtallDestination]?.country}</Flex>
			),
		},
		{
			title: 'Border',
			dataIndex: 'order',
			render: (_,record) => `${allBorders[record.data().roadtallBorder]?.name} - ${allBorders[record.data().roadtallBorder]?.country}`
		},
		{
			title: 'Amount',
			dataIndex: 'roadtallAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().roadtallAmount} 
							  suffix={` Rwf`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="Manage">
						<Button type="primary" className="mr-1" onClick={() => {updateRoadtall(elm.data())}} size="small">Edit</Button>
						<Button danger onClick={() => {showConfirmRoadtall(elm.data())}} size="small">Delete</Button>
					</Tooltip>
				</div>
			)
		}

	];
	return (
		<>
			{!listVisible ? <Card>
				<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
						<Flex className="mb-1" mobileFlex={false}>
							<div className="mr-md-3 mb-3">
								<Title level={4}>Today Requisitions {tDate+'-'+tMonth+'-'+tYear}</Title>
							</div>
						</Flex>
						<div>
							<Button size="small" onClick={newRequisition} type="primary" icon={<PlusCircleOutlined />} block>Add item for requisition</Button>
						</div>
					</Flex>
					<div className="table-responsive p-5">
						{(!reqSalaryLoading && reqSalary.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>SALARIES</Title>
							</div>
							<Table 
								columns={salaryColumns} 
								dataSource={!reqSalaryLoading ? reqSalary.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
							/>
						</div> : null}
						{(!reqRentLoading && reqRent.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>RENT</Title>
							</div>
							<Table 
								columns={rentColumns} 
								dataSource={!reqRentLoading ? reqRent.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
							/>
						</div> : null}
						{(!reqMilleageLoading && reqMilleage.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>MILLEAGE</Title>
							</div>
							<Table 
								columns={milleageColumns} 
								dataSource={!reqMilleageLoading ? reqMilleage.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
							/>
						</div> : null}
						{(!reqFuelLoading && reqFuel.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>Fuel</Title>
							</div>
							<Table 
								columns={fuelColumns} 
								dataSource={!reqFuelLoading ? reqFuel.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
							/>
						</div> : null}
						{(!reqClearingLoading && reqClearing.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>CLEARING</Title>
							</div>
							<Table 
								columns={clearingColumns} 
								dataSource={!reqClearingLoading ? reqClearing.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
								
							/>
						</div> : null}
						{(!reqRoadtallLoading && reqRoadtall.docs.filter(x=> x.data().reqId == reqId).length > 0) ? <div className='mb-5'>
							<div className="mr-md-3 mb-3">
								<Title level={5}>ROADTALL</Title>
							</div>
							<Table 
								columns={roadtallColumns} 
								dataSource={!reqRoadtallLoading ? reqRoadtall.docs.filter(x=> x.data().reqId == reqId) : []} 
								rowKey='_id'
								pagination={false}
								size="small" 
							/>
						</div> : null}
						{grandTotal ? <div className="mt-3">
						
							<div className="d-flex justify-content-end mt-4">
								<div className="text-right ">
								<div className="border-bottom">
								{rentTotal ? <p className="mb-2">
									<span>Total Rent: </span>
									<NumberFormat 
										displayType={'text'} 
										value={rentTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>: null}
								{salaryTotal ? <p className="mb-2">
									<span>Total Salary: </span>
									<NumberFormat 
										displayType={'text'} 
										value={salaryTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p> : ''}
								
								{milleageTotal ? <p className="mb-2">
									<span>Total Milleage: </span>
									<NumberFormat 
										displayType={'text'} 
										value={milleageTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p> : ''}

								{roadtallTotal ? <p className="mb-2">
									<span>Total Roadtall: </span>
									<NumberFormat 
										displayType={'text'} 
										value={roadtallTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p> : ''}
								
								{fuelTotal ? <p className="mb-2">
									<span>Total Fuel: </span>
									<NumberFormat 
										displayType={'text'} 
										value={fuelTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p> : null}
								
								{clearingTotal ? <p className="mb-2">
									<span>Total for clearing: </span>
									<NumberFormat 
										displayType={'text'} 
										value={clearingTotal} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p> : null}
								
							</div>
									<h2 className="font-weight-semibold mt-3">
										<span className="mr-1">Grand Total: </span>
										<NumberFormat 
											displayType={'text'} 
											value={grandTotal} 
											suffix={'Rwf'} 
											thousandSeparator={true}
										/>
									</h2>
								</div>
							</div>
						</div> : null }
					</div>
					<Modal
						title={`New item for requisition`}
						visible={show}
						onCancel={cancelRequisition}

					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									onChange={handleChange}
								>
									{reqTypesLoading ? null : reqTypes.docs.map((type)=><Option value={type.data().value}>{type.data().text}</Option>)}
								</Select>
							</Col>
						</Row>
					</Modal>
				<Form
					layout="vertical"
					form={form}
					name="advanced_search"
					className="ant-advanced-search-form"
				>
					<Modal
						title={`Rent requisition details`}
						visible={showRent}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqRent}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Rent for what"
									name="rentItem"
									rules={[{ message: `Rent item is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{reqToRentLoading ? null : reqToRent.docs.map((rent)=><Option value={rent.data().value}>{rent.data().text}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Rent month"
									name="rentMonth"
									rules={[{ message: `rent month is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<MonthPicker showToday onChange={onMonthChange} className="w-100" placeholder="Select month" />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Amount"
									name="rentAmount"
									rules={[{ required:showRent, message: `Amount is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
					<Modal
						title={`Milleage requisition details`}
						visible={showMilleage}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqMilleage}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Truck"
									name="milleageTruck"
									rules={[{ required: showMilleage,message: `Truck is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{trucksLoading ? null : trucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Destination"
									name="milleageDestination"
									rules={[{ required: showMilleage,message: `destination is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
										className="w-100"
										showSearch
										placeholder="Select destination"
										optionFilterProp="children"
									>
									{!destinationsLoading && destinations.docs.map((address, index) => (
										<Option key={index} value={address.data()._id}>
											{address.data().name+' - '+address.data().country}
										</Option>
									))}
								</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Amount"
									name="milleageAmount"
									rules={[{ required:showMilleage, message: `Amount is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
					<Modal
						title={`Fuel requisition details`}
						visible={showFuel}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqFuel}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Truck"
									name="fuelTruck"
									rules={[{ required: showFuel,message: `Truck is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{trucksLoading ? null : trucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Destination"
									name="fuelDestination"
									rules={[{ required: showFuel,message: `destination is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
										className="w-100"
										showSearch
										placeholder="Select destination"
										optionFilterProp="children"
									>
									{!destinationsLoading && destinations.docs.map((address, index) => (
										<Option key={index} value={address.data()._id}>
											{address.data().name+' - '+address.data().country}
										</Option>
									))}
								</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12}>
								<Form.Item
									label="Liters"
									name="fuelLiters"
									rules={[{ required:showFuel, message: `How many liters were added` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
                      					onChange={handleLiterChange}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12}>
								<Form.Item
									label="Price per liter"
									name="fuelPrice"                      				
									rules={[{ required:showFuel, message: `Price per liter is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										onChange={handlePriceChange}	
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Title level={4}>
									<NumberFormat
										displayType={'text'} 
										value={totalAmount}
										suffix={' RWF'} 
										prefix={'Total: '} 
										thousandSeparator={true} 
									/>
								</Title>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
					<Modal
						title={`Clearing requisition details`}
						visible={showClearing}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqClearing}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Truck"
									name="clearingTruck"
									rules={[{ required: showClearing,message: `Truck is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{trucksLoading ? null : trucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Destination"
									name="clearingDestination"
									rules={[{ required: showClearing,message: `destination is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
										className="w-100"
										showSearch
										placeholder="Select destination"
										optionFilterProp="children"
									>
									{!destinationsLoading && destinations.docs.map((address, index) => (
										<Option key={index} value={address.data()._id}>
											{address.data().name+' - '+address.data().country}
										</Option>
									))}
								</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Amount"
									name="clearingAmount"
									rules={[{ required:showClearing, message: `Amount is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
					<Modal
						title={`Salary requisition details`}
						visible={showSalary}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqSalary}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Salary for what"
									name="salaryEmployee"
									rules={[{ required: showSalary,message: `Salary employee is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{employeesLoading ? null : employees.docs.map((employee)=><Option value={employee.data()._id}>{employee.data().name}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Salary month"
									name="salaryMonth"
									rules={[{ required: showSalary,message: `salary month is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<MonthPicker showToday onChange={onMonthChange} className="w-100" placeholder="Select month" />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Amount"
									name="salaryAmount"
									rules={[{ required:showSalary, message: `Amount is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
					<Modal
						title={`Roadtall requisition details`}
						visible={showRoadtall}
						onCancel={closeOthers}
						confirmLoading={confirmLoading}
						onOk={handleReqRoadtall}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Truck"
									name="roadtallTruck"
									rules={[{ required: showRoadtall,message: `Truck is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
									className="w-100"
									placeholder="-Select-"
									showSearch
									optionFilterProp="children"
									>
										{trucksLoading ? null : trucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12}>
								<Form.Item
									label="Destination"
									name="roadtallDestination"
									rules={[{ required: showRoadtall,message: `destination is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
										className="w-100"
										showSearch
										placeholder="Select destination"
										optionFilterProp="children"
									>
									{!destinationsLoading && destinations.docs.map((address, index) => (
										<Option key={index} value={address.data()._id}>
											{address.data().name+' - '+address.data().country}
										</Option>
									))}
								</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12}>
								<Form.Item
									label="Border"
									name="roadtallBorder"
									rules={[{ required: showRoadtall,message: `which border roadtall was paid` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<Select
										className="w-100"
										showSearch
										placeholder="Select border"
										optionFilterProp="children"
									>
									{!bordersLoading && borders.docs.map((border, index) => (
										<Option key={index} value={border.data()._id}>
											{border.data().name+' - '+border.data().country}
										</Option>
									))}
								</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={24}>
								<Form.Item
									label="Amount"
									name="roadtallAmount"
									rules={[{ required:showRoadtall, message: `Amount is required` }]}
									style={{ display: 'block'}}
									className="w-100" 
								>
									<InputNumber
										className="w-100"
										min={0}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
							</Col>
							{error ? <Col xs={24} sm={24} md={24}>
								<Alert message={error} type="error" showIcon/>
							</Col> : null}
						</Row>
					</Modal>
				</Form>
				
				<hr className="d-print-none"/>
				{grandTotal ? <div className="text-right d-print-none">
				<Button type="primary" className='mr-1' icon={<DownloadOutlined />} size="small">
          			Download PDF
        		</Button>

				<Button type="primary" icon={<CheckCircleOutlined />} size="small">
          			Approve
        		</Button>
				</div> : null}
			</Card>
			:
			<GarageList 
				toolsUsed={toolsUsed} 
				garage={garage} 
				allTrucks={allTrucks}
				allCurrency={allCurrency}
				allCurrencyLoading={allCurrencyLoading}
				closeGarageList={closeGarageList} 
			/>}
		</>
	)
}

export default Garage
